
.custom-bullet {
  margin-bottom: 0;
  li {
    position: relative;
    padding-left: 1.7rem;
    &::before {
      content: '\f00c';
      display: none;
      font-family: "Font Awesome 5 Free";
      font-weight: 900;
    }
  }
  svg, i {
    position: absolute;
    top: 5px;
    left: 0;
  }
}
