.hex-row {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  // width: calc(#{$hex-row} + #{$hex-gap} * 2);
  padding: 0 1rem;
}


.hex-wrap {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 16.6666%;
  height: 100%;
  // padding-bottom: $hex-gap;
  // padding-top: $hex-gap;
}
@media (max-width: 768px) {
  .hex-wrap {
    width: 25%;
  }
}
@media (max-width: 576px) {
  .hex-wrap {
    width: 50%;
  }
}





.hex {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: calc(100% * 0.57735);
  height: 0;
  padding-top: 100%;
  background-color: $miku;
  &::after, &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    background: inherit;
    height: 100%;
    width: 100%;
  }
  &::before {
    transform: rotate(60deg);
  }
  &::after {
    transform: rotate(-60deg);
  }
}
.hex-content-shape {
  position: absolute;
  width: calc(100% + 15.47%);
  height: 100%;
  background: none;
  overflow: hidden;
  transform: rotate(60deg) skewX(30deg) scale(0.94);
  z-index: 1;
}
.hex-content-wrap {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  transform: skewX(-30deg) rotate3d(0,0,1, 300deg);
  &::after, &::before {
    content: '';
    position: absolute;
    left: 0;
    width: 100%;
    height: 50%;
    background-color: rgba(#00cc99, 0.5);
    transition: all 0.15s ease-in;
    z-index: -1;
  }
  &::after {
    top: 0;
    transform: translateY(-100%);
  }
  &::before {
    bottom: 0;
    transform: translateY(100%);
  }
  &:hover::after, 
  &:hover::before, 
  &:hover .hex-content:not(.separate), 
  &:hover .hex-content .hex-content__header, 
  &:hover .hex-content .hex-content__footer {
    transform: translateY(0);
  }
  &:hover .separate {
    opacity: 1;
  }
}
.hex-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  transition: all 0.15s ease-out;
  &:not(.separate) {
    transform: translateY(-100%);
  }
  &.separate {
    opacity: 0;
    .hex-content__header {
      transform: translateY(-300%);
      transition: all 0.25s linear;
    }
    .hex-content__footer {
      transform: translateY(300%);
      transition: all 0.25s linear;
    }
  }
  &-wrap {
    &:hover .hex-content-wrap::after, 
    &:hover .hex-content-wrap::before, 
    &:hover .hex-content:not(.separate), 
    &:hover .hex-content .hex-content__header, 
    &:hover .hex-content .hex-content__footer {
      transform: translateY(0);
    }
    &:hover .separate {
      opacity: 1;
    }
  }
}

.hex-wrap:nth-child(2n+1) {
  margin-top: 16.6666% * 0.57735;
}

.hex-wrap:nth-child(2n+8) {
  margin-top: calc(-9.622% * 0.73);
}

.hex-wrap:nth-child(2n+7) {
  margin-top: calc(9.622% * 0.26);
}


@media (max-width: 768px) {
  .hex-wrap:nth-child(2n+1) {
    margin-top: 25% * 0.57735;
  }
  .hex-wrap:nth-child(2n+5) {
    margin-top: calc(14.433% * 0.26);
  }
  .hex-wrap:nth-child(2n+6) {
    margin-top: calc(-14.433% * 0.73);
  }
}

@media (max-width: 576px) {
  .hex-wrap:nth-child(2n+1) {
    margin-top: 50% * 0.57735;
  }
  .hex-wrap:nth-child(2n+3) {
    margin-top: calc(28.8675% * 0.26);
  }
  .hex-wrap:nth-child(2n+4) {
    margin-top: calc(-28.8675% * 0.73);
  }
}
