body {
  height: 100%;
  font-family: 'Noto Sans TC', sans-serif;
  font-size: 1rem;
  line-height: 170%;
  letter-spacing: 0.8px;
  background-color: #263238;
  overflow-x: hidden;
  overflow-y: scroll;
  &.stop-scrolling {
    height: 100vh;
    overflow: hidden;
  }
}

a {
  text-decoration: none;
}

img {
  vertical-align: middle;
  max-width: 100%;
  height: auto;
}

::-webkit-scrollbar {
  width: 0.35rem;
  background-color: transparent;
}

::-webkit-scrollbar-track {
  background-color: lighten($dark, 3%);
}

::-webkit-scrollbar-thumb {
  background-color: $miku;
}

.f-ubuntu {
  font-family: 'Ubuntu', sans-serif;
}

/* --------------------------------------------------------*/
/* Hero                                                    */
/* --------------------------------------------------------*/

.hero {
  width: 100vw;
  height: 100vh;
  min-height: 500px;
  display: flex;
  overflow: hidden;
  background-color: #263238;
  z-index: 110;
}

.hero-primary {
  position: absolute;
  bottom: calc(50% - 200px);
  left: 50%;
  display: flex;
  flex-wrap: wrap;
  max-width: 380px;
  font-family: 'Ubuntu', sans-serif;
  font-weight: 600;
  color: $light;
  cursor: default;
  z-index: 1;
  @include media (768) {
    left: 5%;
    bottom: 40%;
  }
  .ab {
    display: block;
    opacity: 0;
    text-shadow: 0 3px 5px rgba(#000, 0.15);
    animation-fill-mode: forwards;
    transition: color 0.3s; 
    &.rubberBand {
      color: $orange;
    }
    &.em {
      color: $primary;
    }
  }
}

.hero-sub {
  position: relative;
  top: 50%;
  flex: 0 1 28rem;
  max-width: 45rem;
  // flex: 0 1 437px;
  // max-width: 729px;
  width: 100%;
  height: 100%;
  font-family: 'Ubuntu', sans-serif;
  letter-spacing: 0.05rem;
  color: rgba($light, 0.3);
  background-color: #2E3440;
  border-radius: 0.5rem;
  box-shadow: 0px 5px 2rem rgba(#000, 0.15);
  overflow: hidden;
  transform: translateY(-50%);
  @include media(768) {
    flex: 0 1 22rem;
    // top: 0;
    // transform: translateY(0);
    opacity: 0.3;
    pointer-events: none;
  }
  &-text {
    position: absolute;
    top: 60px;
    left: 65px;
    color: rgba($light, 0.8);
    @include media(420) {
      top: 21%;
      left: 0;
    }
  }
  &.animated {
    animation-delay: 0.5s;
  }
}

.editor-sidebar {
  padding: 1rem 0 0.6rem 0;
  width: 3rem;
  height: 100%;
  font-size: 1.4rem;
  @include media(420) {
    display: none!important;
  }
  a {
    color: rgba($light, 0.8);
    transition: color 0.3s linear;
    &:hover {
      color: darken($orange, 5%);
    }
  }
}

.editor-top {
  position: absolute;
  top: 0;
  left: 3rem;
  padding: 0 1rem;
  width: calc(100% - 3rem);
  font-size: 1.2rem;
  box-shadow: 0px 5px 3px -2px rgba(0, 0, 0, 0.13);
  @include media(420) {
    display: none!important;
  }
  .editor-tab {
    padding: 0.3rem 0;
    &.active {
      color: rgba($light, 0.9);
      background-color: rgba(#fff, 0.08);
    }
  }
}

.scrolldown {
  cursor: pointer;
  transition: all 0.2s linear;
  z-index: 102;
  &:hover {
    padding-bottom: 0.5rem;
  }
}
.mousedown {
  width: 30px;
  height: 50px;
  background: url('../images/mousedown.svg') no-repeat;
  filter: brightness(100) opacity(0.8);
}

.down {
  color: rgba(#fff, 0.8);
  z-index: 1;
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-direction: alternate; 
  animation-fill-mode: both;
  animation-timing-function: ease-in;
}

/* --------------------------------------------------------*/
/* Main                                                    */
/* --------------------------------------------------------*/

.main {
  // overflow: hidden;
  header {
     margin-bottom: 3rem;
     h2 {
      font-family: 'Ubuntu', sans-serif;
      letter-spacing: 0.8px;
      // &::before, &::after {
      //   display: inline-block;
      //   font-size: 1rem;
      //   color: rgba($dark, 0.2);
      //   transform: translateY(-2px);
      // }
      // &::before {
      //   content: '<h2>';
      //   margin-right: 1rem;
      // }
      // &::after {
      //   content: '</h2>';
      //   margin-left: 1rem;
      // }
    }
    .subtitle {
      font-family: 'Fredericka the Great', cursive;
      font-size: 1.6rem;
      color: $gray-600;
    }
  }
}

.divider {
  margin: 1rem auto 1.8rem auto;
  width: 5rem;
  height: 4px;
  background-color: lighten($dark, 5%);
}

section {
  padding: 3.5rem 0;
  &#contact {
    padding-bottom: 5.5rem;
  }
  header * {
    opacity: 0;
    &.animated {
      animation-duration: 0.5s;
    }
  }
}

.wp {
  opacity: 0;
  animation-duration: 0.8s;
  animation-fill-mode: both;
}

.popup {
  animation-name: popup;
  animation-timing-function: ease-in-out;
}

@keyframes popup {
  0% {
    opacity: 0;
    transform: scale(0);
  }
  60% {
    opacity: 1;
  }
  80% {
    transform: sclae(0.95);
  }
  90% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}
/* Navigation                                              */

.navigation {
  position: sticky;
  top: 0;
  right: 0;
  left: 0;
  display: flex;
  align-items: center;
  padding: 0 1.5rem;
  font-family: 'Ubuntu', sans-serif;
  font-size: 1.2rem;
  letter-spacing: 0.8px;
  opacity: 1;
  transform: translateY(0);
  transition: all 0.4s ease;
  z-index: 100;
  &.hide {
    opacity: 0;
    transform: translateY(-150%);
  }
  .nav-menu {
    display: flex;
    align-items: center;
    margin-left: auto;
    @include media(767) {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      display: block;
      padding: 0 1.2rem 1rem 1.2rem;
      padding-top: calc(54.39px + 0.8rem + 3px);
      background-color: lighten($dark, 3%);
      box-shadow: 0px 5px 10px rgba(#000, 0.1);
      opacity: 0;
      z-index: -2;
    }
    &.menu-open {
      @include media(767) {
        animation: menuOpen 0.25s ease-out;
        animation-fill-mode: forwards;
        // transform: translateY(0);
      }
    }
    &.menu-close {
      @include media(767) {
        animation: menuClose 0.3s ease-out;
        animation-fill-mode: forwards;
      }
    }
    li {
      padding: 0.9rem;
      margin-bottom: 1.5px;
      transition: background 0.25s linear;
      &:not(.logo):hover {
        background-color: lighten($dark, 10%);
      }
      &.active a, &:hover a {
        color: darken($orange, 5%);
      }
    }
    a {
      text-decoration: none;
      transition: color 0.3s linear 0.1s;
    }
  }
  .logo {
    display: block;
    color: $light;
    text-decoration: none;
    transition: color 0.3s linear 0.1s;
    &:hover {
      color: darken($orange, 5%);
    }
    @include media(767) {
      padding: 0.8rem 0;
    }
  }
  .nav-bg-layer {
    position: absolute;
    left: 0;
    right: 0;
    height: calc(100% + 3px);
    border-bottom: 3px solid #56c8d8;
    background-color: lighten($dark, 3%);
    z-index: -1;
  }
}

.menu-trigger {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 44px;
  height: 44px;
  cursor: pointer;
  .dash {
    position: relative;
    display: block;
    width: 30px;
    height: 4px;
    background-color: $light;
    border-radius: 1px;
    pointer-events: none;
    &::before, &::after {
      content: '';
      position: absolute;
      left: 0;
      width: 100%;
      height: 4px;
      background-color: $light;
      border-radius: 1px;
    }
    &::before {
      top: -10px;
    }
    &::after {
      top: 10px;
    }
  }
}

@keyframes menuOpen {
  0% {
    opacity: 0;
    transform: translateY(-100%);
  }
  90% {
    opacity: 1;
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes menuClose {
  0% {
    opacity: 1;
    transform: translateY(0);
  }
  50% {
    transform: translateY(30px);
  }
  90% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    transform: translateY(-100%);
  }
}

/* Profile                                              */

.photo-wrap {
  flex: 0 0 auto;
  margin-top: 35.47px;
  margin-right: -8px;
  width: calc(9rem * 1.1547);
  height: 9rem;
  background-color: $miku;
  clip-path: polygon(25.1325% 0%, 74.8675% 0%, 100% 50%, 74.8675% 100%, 25.1325% 100%, 0% 50%);
  @include media(992) {
    margin-top: 0;
  }
  @include media(768) {
    margin-top: 35.47px;
  }
  @include media(420) {
    margin-top: 0;
  }
}
.profile__photo {
  width: 100%;
  height: 100%;
  transform: scale(0.92);
  overflow: hidden;
  clip-path: polygon(25.1325% 0%, 74.8675% 0%, 100% 50%, 74.8675% 100%, 25.1325% 100%, 0% 50%);
  .photo {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

#profile {
  .wrap {
    flex: 0 0 auto;
    order: 2;
    width: 200px;
    @include media(420) {
      display: none;
    }
  }
  .skill-box-wrap {
    display: block;
    cursor: pointer;
    &.hex-placement {
      .hexbox {
        background-color: transparent;
        pointer-events: none;
        &::after, &::before {
          background-color: inherit;
        }
      }
    }
    .skill-box.active .hexbox {
      background-color: darken($orange, 5%);
    }
    .hexbox {
      transition: all 0.2s linear;
      &::after, &::before {
        background-color: inherit;
      }
    }
  }
  .skill-box-wrap:nth-child(2n) {
    float: right;
    margin-left: 50px;
    margin-top: -42.47px;
  }
  .skill-box-wrap:not(:first-child):nth-child(2n+1) {
    float: left;
    margin-top: -42.57px;
  }
}
.header__photo {
  width: 200px;
  height: 200px;
  background-image: url(../images/IMG_1345.JPG);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
}

.personality {
  position: relative;
  top: -30px;
  font-size: 2.2rem;
  color: $primary;
  @include media(992) {
    top: 0;
  }
  @include media(767) {
    top: -30px;
  }
  @include media(420) {
    display: none;
  }
}

/* Skills                                              */
#skill {
  background-color: lighten(#cfd8dc, 5%);
}
.skill-box-wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.skill-box {
  display: flex;
  justify-content: center;
  width: 100px;
  height: 100px;
  transition: all 0.2s ease-out;
  > .hexbox {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: calc(100% * 0.57735);
    height: 100%;
    background-color: $miku;
    z-index: 1;
    &::after, &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: $miku;
      z-index: -1;
    }
    &::after {
      transform: rotate(60deg);
    }
    &::before {
      transform: rotate(-60deg);
    }
  }
}
.skill-description {
  max-width: 70vw;
  h3 {
    font-family: 'Ubuntu', sans-serif;
    text-align: center;
  }
  p {
    color: $gray-700;
  }
}

.skillbar-description {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  padding: 0 1rem;
  width: calc(100% * 0.7124857);
  height: 100%;
  opacity: 0;
  font-size: 1.1rem;
  visibility: hidden;
  transform: translateX(-100%);
  transition: all 0.3s ease-in;
  @include media(767) {
    position: relative;
    display: none;
    margin-top: 1rem;
    width: 100%;
    font-size: 0.9rem;
    opacity: 1;
    visibility: visible;
    transform: translateX(0);
    transition: none;
  }
  h4 {
    margin-right: 1.5rem;
    margin-bottom: 0;
  }
  &.show {
    opacity: 1;
    visibility: visible;
  }
}
.bg-icon {
  position: absolute;
  top: 50%;
  left: 50%;
  color: rgba($light, 0.8);
  font-size: 12rem;
  transform: translate(-50%, -50%);
  pointer-events: none;
  z-index: 0;
  &::after {
    content: '\f121';
    display: none;
    font-family: "Font Awesome 5 Free";
    font-weight: 900;
  }
}
.skillbar-wrap {
  @include media(577) {
    overflow: hidden;
  }
  &:not(:first-child) {
    margin-top: 1rem;
  }
}
.skillbar {
  position: relative;
  display: flex;
  justify-content: flex-start;
  align-items: stretch;
  border-radius: 3px;
  background-color: rgba($light, 0.9);
  overflow: hidden;
  cursor: pointer;
  z-index: 1;
  &-title {
    flex: 0 0 16%;
    min-width: 80px;
    padding: 0.15rem 0;
    font-family: 'Ubuntu', sans-serif;
    font-weight: 600;
    font-size: 0.8rem;
    text-align: center;
    color: $gray-700;
    background-color: rgba($miku, 0.7);
    pointer-events: none;
    @include media(768) {
      flex: 0 0 24%;
    }
    @include media(576) {
      flex: 0 0 27%;
    }
  }
  &-rate {
    flex: 1 1 100%;
    pointer-events: none;
    > div {
      height: 100%;
      background-color: darken($miku, 10%);
      transition: width 1s ease-in-out;
    }
  }
  &-state {
    position: absolute;
    top: 50%;
    right: 0;
    width: 100px;
    align-self: center;
    margin-left: auto;
    padding: 0 1.3rem;
    font-family: 'Ubuntu', sans-serif;
    font-size: 0.9rem;
    text-align: right;
    color: $gray-600;
    transform: translateY(-50%);
    pointer-events: none;
    @include media(768) {
      padding: 0 0.8rem;
    }
  }
  &.active {
    .skillbar-title {
      color: $light;
      background-color: lighten($primary, 15%);
    }
    .skillbar-rate > div {
      background-color: darken($primary, 10%);
    }
  }
}


/* Portofolio                                              */

.hex-row {
  width: 90%;
  margin: 0 auto;
}
.hex-content-wrap {
  cursor: pointer;
  &::after, &::before {
  background-color: rgba($miku, 0.95);
  }
}
.hex-content {
  color: $dark;
  h3 {
    margin-bottom: 0;
    font-size: 1.3rem;
  }
}
.portofolio-present {
  padding: 3rem 1.5rem 2.5rem 1.5rem;
  background-color: #cfd8dc;
  @include media(767) {
    padding: 1rem 0;
  }
  .present-panel {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    opacity: 0;
    visibility: hidden;
    transform: translateX(-100%);
    transition: all 0.3s ease;
    &.show {
      position: relative;
      top: 0;
      left: 0;
      opacity: 1;
      visibility: visible;
      transform: translateX(0);
    }
  }
}
.tag-wrap {
  margin-top: 0.7rem;
}

.present-panel {
  .carousel {
    display: flex;
    flex-direction: column;
  }
  .carousel-indicators {
    position: relative;
    order: 2;
    margin-top: 0.8rem;
    margin-bottom: 0;
    li {
      width: 8px;
      height: 8px;
      border-radius: 50%;
      border-top: none;
      border-bottom: none;
      background-color: #333;
      box-shadow: 0 3px 5px -2px rgba(#000, 0.5);
      opacity: 0.3;
      &.active {
        opacity: 1;
      }
    }
  }
  .carousel-item {
    min-height: 300px;
    will-change: transform;
  }
}


/* Contact                                             */

// ::placeholder {
//   color: transparent !important;
// }
#contact {
  .form-control {
    background-color: transparent;
    border: 1.5px solid $primary;
  }
}
.submit-form {
  width: 8rem;
  letter-spacing: 1.4px;
  text-transform: uppercase;
}

/* Footer                                             */

.footer {
  position: relative;
  &__link {
    font-size: 1.5rem;
    li {
      width: 3.5rem;
      height: 3.5rem;
      text-align: center;
      line-height: 3.5rem;
      border-radius: 50%;
      background-color: rgba($primary, 0.5);
      overflow: hidden;
      transition: background 0.3s linear;
      &:not(:first-child) {
        margin-left: 1.2rem;
      }
      &:hover {
        background-color: darken($miku, 5%);
        svg {
          animation: bottomToTop 0.2s ease-out;
        }
      }
    }
    a {
      display: block;
      color: $light;
    }
  }
  &__copyright {
    color: $light;
  }
}
.totop {
  position: absolute;
  top: -25px;
  left: 50%;
  display: block;
  padding: 0.6rem 0.8rem;
  font-size: 1.5rem;
  color: $light;
  border-radius: 4px;
  background-color: $orange;
  transform: translate3d(-50%, 0, 0);
  &-animation {
    animation-fill-mode: backward;
    animation: toTop 1s ease-in;
  }
}

@keyframes bottomToTop {
  50% {
    transform: translateY(100%);
    opacity: 0;
  }
  51% {
    transform: translateY(-100%);
    opacity: 1;
  }
  100% {
    transform: (0);
  }
}

@keyframes toTop {
  20% {
    -webkit-transform: translate3d(-50%, 0, 0);
    transform: translate3d(-50%, 0, 0);
  }

  40%,
  45% {
    opacity: 1;
    -webkit-transform: translate3d(-50%, 20px, 0);
    transform: translate3d(-50%, 20px, 0);
  }

  to {
    opacity: 0;
    -webkit-transform: translate3d(-50%, -2000px, 0);
    transform: translate3d(-50%, -2000px, 0);
  }
}
